import * as React from "react";
const styles = require("./styles/aside.less");
export default class UCAsideView extends React.Component {
    constructor(props) {
        super(props);
    }
    render() {
        return React.createElement("div", { className: styles.ucAsideView }, "UCAsideView");
    }
}



